/* z-index */
.mint-steps {
  overflow: hidden;
  background-color: #fff;
}
.mint-steps--horizontal {
  padding: 0 10px;
}
.mint-steps--horizontal .mint-steps__items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 10px;
  overflow: hidden;
  position: relative;
  padding-bottom: 32px;
}
.mint-steps--horizontal .mint-steps__items.mint-steps__items--alone {
  padding-top: 10px;
}
.mint-steps--vertical {
  padding: 0 0 0 150px;
}
.mint-steps__icon {
  float: left;
  margin-right: 10px;
}
.mint-steps .mint-icon {
  font-size: 23px;
  line-height: 1;
}
.mint-steps__message {
  display: table;
  height: 40px;
  margin: 15px 0;
}
.mint-steps__message .mint-steps__message-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.mint-steps__title {
  font-size: 14px;
  color: #333;
}
.mint-steps__desc {
  font-size: 12px;
  line-height: 1.5;
  color: #999;
  max-height: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}



