.mint-checkbox-new-row{
    display: block;
    padding: 10px 0;
    text-align: left;
}
.mint-checkbox-new{}
.mint-checkbox-new.is-right{
    float: right;
}
.mint-checkbox-new-label{
    vertical-align: middle;
    margin-left: 6px;
    color:#1e2329;
}
.mint-checkbox-new-input{
    display: none;
}
.mint-checkbox-new-input:checked + .mint-checkbox-new-core{
    background-color: #06c1ae;
    border-color: #06c1ae;
}
.mint-checkbox-new-input:checked + .mint-checkbox-new-core::after{
    border-color: #fff;
    -webkit-transform: rotate(45deg) scale(1);
            transform: rotate(45deg) scale(1);
}
.mint-checkbox-new-input[disabled] + .mint-checkbox-new-core{
    background-color: #d9d9d9;
    border-color: #ccc;
}
.mint-checkbox-new-core{
    display: inline-block;
    background-color: #fff;
    border-radius: 100%;
    border: 1px solid #ccc;
    position: relative;
    width: 20px;
    height: 20px;
    vertical-align: middle;
}
.mint-checkbox-new-core::after{
    border: 2px solid transparent;
    border-left: 0;
    border-top: 0;
    content: " ";
    top: 3px;
    left: 6px;
    position: absolute;
    width: 4px;
    height: 8px;
    -webkit-transform: rotate(45deg) scale(0);
            transform: rotate(45deg) scale(0);
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.disabled{
    color:#2f343b;
}



