.mint-fixed-container{
  position: fixed;
}
.mint-fixed-container--top{
  top: 0;
  left: 0;
}
.mint-fixed-container--bottom{
  bottom: 0;
  left: 0;
}
.mint-fixed-container:after{
  content: "";
  display: table;
  clear: both;
}



