/* z-index */
.mint-navbar {
    background-color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    border-top: 0.5px solid #e8e8e8;
    border-bottom: 0.5px solid #e8e8e8;
}
.mint-navbar .mint-tab-item {
    padding: 17px 0;
    font-size: 15px
}
.mint-navbar .mint-tab-item:last-child {
    border-right: 0;
}
.mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #06c1ae;
    color: #06c1ae;
}
/* margin-bottom: -3px; */
.mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
    color: #06c1ae;
}
.mint-navbar.is-fixed {
    top: 0;
    right: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}



