.mint-cell{
  background-color: #fff;
  box-sizing: border-box;
  color: inherit;
  min-height: 50px;
  display: block;
  overflow: hidden;
  position: relative;
  text-decoration: none;
}
.mint-cell img{
  vertical-align: middle;
}
.mint-cell:first-child .mint-cell-wrapper{
  background-origin: border-box;
}
.mint-cell:last-child{
  background-image: -webkit-linear-gradient(bottom, #d9d9d9, #d9d9d9 50%, transparent 50%);
  background-image: linear-gradient(0deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: bottom;
}
.mint-cell-disabled{}
.mint-cell-disabled-color{
  color: #92969c;
}
.mint-cell-wrapper{
  border-top:0.5px solid #e8e8e8;
  background-size: 120% 1px;
  background-repeat: no-repeat;
  background-position: top left;
  background-origin: content-box;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 16px;
  line-height: 1;
  min-height: inherit;
  overflow: hidden;
  width: 100%;
}
.mint-cell-mask{}
.mint-cell-mask::after{
  background-color: #000;
  content: " ";
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
}
.mint-cell-mask:active::after{
  opacity: .1;
}
.mint-cell-text{
  vertical-align: middle;
}
.mint-cell-label{
  color: #888;
  display: block;
  font-size: 12px;
  margin-top: 6px;
}
.mint-cell-title{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  min-width: 68px;
  margin-right: 8px;
}
.mint-cell-value{
  color: #888;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.mint-cell-value.is-link{
  /* margin-right: 8px; */
}
.mint-cell-left{
  position: absolute;
  height: 100%;
  left: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.mint-cell-right{
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.mint-cell-newline{
  margin-bottom: 14px;
}
.mint-cell-no-top-line{
  border-top:none;
}
.mint-cell-allow-right::after{
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 50%;
  right: 20px;
  position: absolute;
  width: 5px;
  height: 5px;
  -webkit-transform: translateY(-50%) rotate(45deg);
          transform: translateY(-50%) rotate(45deg);
}
.icon{
  color:#92969c;
  font-size: 24px;
}



